<template>
  <section class="Brand">
    <ul class="Brand-ul">
      <li>品牌制造商直供</li>
      <li>更多<i class="iconfont icon-arrow-left"></i></li>
    </ul>
    <brand-as :item="n" :idx="i" :key="i" v-for="(n,i) in br"></brand-as>
  </section>
</template>

<script>
    import BrandAs from "./Brand/BrandAs";
    export default {
        name: "IndexBrand",
        props:["br"],
      components: {BrandAs}
    }
</script>

<style scoped>
  .Brand{
    width: 100%;
    height: 3.49rem;
    background: white;
  }
  .Brand-ul {
    display: flex;
    margin-left: 0.17rem;
    height: 0.54rem;
    align-items: center;
    font-size: 0.17rem;
  }

  .Brand-ul li .icon-arrow-left {
    font-size: 0.15rem;
  }

  .Brand-ul li:nth-child(2) {
    margin-left: 1.7rem;
  }
</style>



