<template>
  <div class="AAA">
    <carts-header></carts-header>
    <div class="shen">
      <carts-puct></carts-puct>
      <cart-list></cart-list>
    </div>
    <cart-footer></cart-footer>
  </div>

</template>

<script>
  import CartsPuct from "../components/carts/CartsPuct";
  import CartsHeader from "../components/carts/CartsHeader";
  import CartList from "../components/carts/CartList";
  import CartFooter from "../components/carts/CartFooter";

  export default {
    name: "WangYiCarts",
    components: {CartFooter, CartList, CartsHeader, CartsPuct}
  }
</script>

<style scoped>
  .AAA {
    height: 100%;
    display: flex;
    flex-direction: column;
  }

  .shen {
    width: 100%;
    overflow: hidden;
    flex: 1;
    overflow-y: scroll;
  }
</style>



