<template>
  <div class="AAA">
    <index-header></index-header>
    <div class="shen">
      <clothing-tit></clothing-tit>
      <costume-coco :co="cost.lists"></costume-coco>
    </div>
    <index-footer></index-footer>
  </div>

</template>

<script>
  import IndexHeader from "../components/index/IndexHeader";
  import IndexFooter from "../components/index/IndexFooter";
  import ClothingTit from "../components/clothing/ClothingTit";
  import CostumeCoco from "../components/clothing/CostumeCoco";
  const AJAXURL = 'http://127.0.0.1:5000/aaa';
  export default {
    name: "WangYiClothing",

    data() {
      return {
        cost: []
      }
    },
    created() {
      this._initPageData()
    },
    components: {CostumeCoco, ClothingTit, IndexFooter, IndexHeader},
    methods: {

      _initPageData() {
        fetch(AJAXURL).then(res => {
          res.json().then(data => {
            this.cost = data
            console.log(data)
          })
        })
      }

    }
  }
</script>

<style scoped>
  .AAA {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .shen {
    width: 100%;
    overflow: hidden;
    flex: 1;
    overflow-y: scroll;
    height: 100%;
    background: #ffffff;
  }
</style>



