<template>
  <section class="list">
    <div class="home-header">
      <p>床品件套</p>
      <p>MUJI等品牌制造商出品</p>
    </div>
    <product-list :item="n" :idx="i" :key="i"    v-for="(n,i) in lit"></product-list>
    <div class="backtop">
      <i class="iconfont icon-arrowDown"></i>
    </div>
  </section>
</template>

<script>
  import ProductList from "./Productlist";

  export default {
    name: "HomeList",
    props:["lit"],
    components: {ProductList}
  }
</script>

<style scoped>
  .list {
    width: 100%;
    height: 100%;
    background: white;
  }

  .home-header {
    width: 100%;
    height: 0.6rem;
  }

  .home-header p {
    text-align: center;
  }

  .home-header p:last-child {
    font-size: 0.14rem;
    color: #acacac;

  }

  .home-header p:first-child {
    font-size: 0.16rem;
    padding-top: 0.08rem;
    line-height: 0.23rem;
  }

  .backtop {
    width: 0.5rem;
    height: 0.5rem;
    background: white;
    border-radius: 50%;
    border: 1px solid #f6f6f6;
    position: fixed;
    right: 1%;
    display: flex;
    bottom: 10%;
    align-items: center;
    justify-content: center;
  }
  .icon-arrowDown{
    font-size: 0.2rem;
    display: block;

  }
</style>



