import Vue from 'vue'
import Router from 'vue-router'
import WangYiIdex from '../page/WangYiIndex'
import WangYiHome from '../page/WangYiHome'
import WangYiCoomodity from  '../page/WangYiCoomodity'
import WangYiClothing from  '../page/WangYiClothing'
import WangYiParticulars from  '../page/WangYiParticulars'
import WangYiCarts from  '../page/WangYiCarts'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'WangYiIdex',
      component: WangYiIdex
    },
    {
      path: '/home',
      name: 'WangYiHome',
      component: WangYiHome
    },
    {
      path: '/coom',
      name: 'WangYiCoomodity',
      component: WangYiCoomodity
    },
    {
      path: '/cloo',
      name: 'WangYiClothing',
      component: WangYiClothing
    },
    {
      path: '/part',
      name: 'WangYiParticulars',
      component: WangYiParticulars
    },
    {
      path: '/cart',
      name: 'WangYiCarts',
      component: WangYiCarts
    }
  ]
})



