<template>
  <section class="private-custom">
    <p>私人定制</p>
    <div class="custom-carousel">
      <div class="swiper-container s2">
        <div class="swiper-wrapper">
          <div class="swiper-slide" :key="i" v-for="(n,i) in snw"><img :src="n.simgs" ></div>


        </div>
        <!-- Add Pagination -->
        <div class="swiper-pagination"></div>
      </div>
    </div>
  </section>
</template>

<script>
    export default {
        name: "IndexPrivateCustom",
      props:["snw"],
      updated() {
        new Swiper(' .s2', {
          slidesPerView: 3,
          spaceBetween: 30,
          slidesPerGroup: 3,

          loopFillGroupWithBlank: true,
          pagination: {
            el: '.swiper-pagination',
            clickable: true,
          },
          navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev',
          },
        })
      }
    }
    import Swiper   from "swiper"
    import "../../../node_modules/swiper/dist/css/swiper.css";
</script>

<style scoped>
  .private-custom {
    margin-top: 0.1rem;
    margin-bottom: 0.1rem;
    width: 100%;
    height: 2.5rem;
    background: #ffffff;
  }

  .private-custom p {
    font-size: 0.16rem;
    height: 0.54rem;
    line-height: 0.54rem;
    margin-left: 0.17rem;
  }

  .custom-carousel {
    width: 100%;
    height: 1.96rem;
  }
  .custom-carousel .s2 .swiper-pagination {
    top: 38%;
    left: 0;
  }
  .s2 img{
    width: 1rem;
    height: 1rem;
    background: #f5f5f5;
  }
</style>
<style>
  .custom-carousel .s2 .swiper-pagination-bullet{
    background: grey;
    opacity: 0.5;
    width:0.06rem;
    height:0.06rem;
    border-radius:50% ;

  }
  .custom-carousel .s2 .swiper-pagination-bullet-active {

    background:darkred;
    border-radius:50% ;
  }
</style>



