<template>
  <section class="daily">
    <daily-as :key="da.id" :tits="n.tit" v-for="(n,i) in da"></daily-as>
    <daily-ul :key="da.id" :navss="n.navs" v-for="(n,i) in da"></daily-ul>
  </section>
</template>

<script>

  import DailyAs from "./Daily/DailyAs";
  import DailyUl from "./Daily/DailyUl";

  export default {
    props: ["da"],
    components: {DailyUl, DailyAs}
  }
</script>

<style scoped>
  .daily {
    width: 100%;
    height: 4.13rem;
    background: white;
    margin-top: 0.1rem;
    overflow: hidden;
  }
</style>



