<template>
  <div class="AAA">
    <index-header></index-header>
    <div class="shen">
      <home-title></home-title>
      <home-list :lit="home.list"></home-list>
    </div>
    <index-footer></index-footer>
  </div>
</template>

<script>
  import IndexHeader from "../components/index/IndexHeader";
  import IndexFooter from "../components/index/IndexFooter";
  import HomeTitle from "../components/home/HomeTitle";
  import HomeList from "../components/home/HomeList";

  const AJAXURL = 'http://127.0.0.1:5000/aaa';
  export default {
    name: "WangYiHome",
    data() {
      return {
        home: []
      }
    },
    created() {
      this._initPageData()
    },
    components: {HomeList, HomeTitle, IndexFooter, IndexHeader},
    methods: {

      _initPageData() {
        fetch(AJAXURL).then(res => {
          res.json().then(data => {
            this.home = data
            console.log(data)
          })
        })
      }

    }
  }
</script>

<style scoped>
  .AAA {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .shen {
    width: 100%;
    overflow: hidden;
    flex: 1;
    overflow-y: scroll;
    height: 100%;
    background: #ffffff;
  }
</style>



