<template>
  <div>
    <header>
      <ul class="top">
        <li><img src="../../../static/IndexImages/logo.png" alt=""></li>
        <li>
          <input type="text" placeholder="衬衫" class="inputA">
          <i class="iconfont icon-sousuo"></i>
        </li>
        <li>
          <button>登录</button>
        </li>
      </ul>
      <section class="bottom">
        <ul class="left">
          <li class="current">
            <router-link to="/">推荐</router-link>
          </li>
          <li>
            <router-link to="/home">居家</router-link>
          </li>
          <li>鞋包配饰</li>
          <li>
            <router-link to="cloo">服装</router-link>
          </li>
          <li>电器</li>
          <li>洗护</li>
          <li>饮食</li>
          <li>餐厨</li>
          <li>婴童</li>
          <li>文体</li>
        </ul>
        <span class="right" @click="changeIsShow">
          <i class="iconfont icon-icon"></i>
        </span>
      </section>
    </header>
    <section class="button-none" v-show="isShow">
      <ul class="nonea">
        <li>推荐</li>
        <li>居家</li>
        <li>鞋包配饰</li>
        <li>服装</li>
        <li>电器</li>
        <li>洗护</li>
        <li>饮食</li>
        <li>餐厨</li>
        <li>婴童</li>
        <li>文体</li>
      </ul>
      <div class="mueen"></div>
    </section>
  </div>
</template>

<script>
  // import $ from "jquery"

  export default {
    name: "indexHeader",
    data(){
      return{
        isShow:false
      }
    },

    methods: {

      changeIsShow(){
        this.isShow = !this.isShow;
      }
    }

  }
</script>

<style scoped>
  header {
    width: 100%;
    height: 0.79rem;
    background: #ffeadf;
    border-bottom: 3px solid #d9d9d9;
  }



  .top {
    width: 100%;
    height: 0.46rem;
    border-bottom: 1px solid #ffede5;
    display: flex;
    align-items: center;
    justify-content: space-around;
  }

  .top img {
    display: block;
    width: 0.69rem;
    height: 0.2rem;
  }

  .top input {
    display: block;
    width: 2.36rem;
    height: 0.3rem;
    border: none;
    border-radius: 0.05rem;
    font-size: 0.12rem;
    position: relative;
    text-indent: 0.30rem;
  }

  .top .icon-sousuo {
    font-size: 0.2rem;
    color: #999999;
    position: absolute;
    left: 0.85rem;
    top: 0.12rem;
  }

  .top button {
    width: 0.39rem;
    height: 0.24rem;
    display: block;
    background: #ffede3;
    border-radius: 0.05rem;
    border: 1px solid #b4282d;
    margin: 0 auto;
    font-size: 0.12rem;
    color: #b4282d;
  }

  /*bottom*/
  .bottom {
    width: 100%;
    height: 0.33rem;
    display: flex;
  }

  .bottom .left {
    width: 80%;
    display: flex;
    white-space: nowrap;
    overflow-x: auto;
    overflow-y: hidden;
  }

  .left li {
    padding: 0 0.1rem;
    display: inline-block;
    font-size: 0.13rem;
    line-height: 0.3rem;
    flex-shrink: 0;
    text-align: center;
    margin: 0 0.1rem 0 0.1rem;
  }
  .current{
    border-bottom: 5px solid crimson;
  }

  /*.left li:nth-child(1) {*/
    /*border-bottom: 5px solid crimson;*/
  /*}*/

  .bottom .right {
    width: 20%;
  }

  .icon-icon {
    font-size: 0.23rem;
    display: block;
    text-align: center;
  }

  .button-none {
    width: 100%;
    height: 100%;
    /*display: none;*/
    position: fixed;
    z-index: 999;

  }

  .nonea {
    width: 100%;
    height: 20%;
    display: flex;
    flex-wrap: wrap;
    background: white;

  }

  .nonea li {
    width: 0.7rem;

    border: 1px solid #b4282d;
    text-align: center;
    font-size: 0.13rem;
    line-height: 0.22rem;
    margin: 0.1rem 0.1rem;
    border-radius: 0.15rem;
  }

  .mueen {
    width: 100%;
    height: 70%;
    background: #282828;
    opacity: 0.4;

  }
</style>



