<template>
    <section>
      <div class="bottom" >
        <img src="../../../static/partimages/port_img.jpg" alt="">
      </div>
      <span>大家都在看</span>
      <div>
        <do-you-like :key="i"  :item="n" :idx="i" v-for="(n,i) in pa"></do-you-like>
      </div>
    </section>
</template>

<script>
    import DoYouLike from "./DoYouLike";
    export default {
        name: "PartBottom",
      props:["pa"],
      components: {DoYouLike}
    }
</script>

<style scoped>
  section{
    width: 100%;
    height: 100%;
    background: #ffffff;
  }
  .bottom img{
    width: 100%;
    height: 100%;
  }
  span {
    font-size: 0.18rem;
    display: block;
    text-align: center;
    margin-bottom: 0.5rem;
  }
</style>



