<template>
  <li class="libox">
    <input class="fuxuan" type="checkbox">
    <a href=""><img src="" alt=""></a>
    <ul class="ullist">
      <li>泰国制造 香脆椰子卷 16克*12份</li>
      <li>原味 192克</li>
      <li>￥29.00</li>
    </ul>
    <ul class="inputbox">
      <li><input type="button" value="-"></li>
      <li><input type="text" value="1"></li>
      <li><input type="button" value="+"></li>
    </ul>
  </li>

</template>

<script>
  export default {
    name: "CartsLists"
  }
</script>

<style scoped>
  .libox {
    height: 1.1rem;
    width: 100%;
    display: flex;
    position: relative;
  }

  .fuxuan {
    width: 0.2rem;
    height: 0.2rem;
    display: block;
    margin: 0.45rem 0.15rem 0 0.15rem;
  }

  a {
    display: block;
    width: 0.85rem;
    height: 0.85rem;
    background: #f4f4f4;
    border-radius: 5px;
    margin-top: 0.12rem;
  }

  .ullist {
    font-size: 0.14rem;
    margin-top: 0.12rem;
    margin-left: 0.13rem;
  }
  .ullist li:nth-child(2){
    font-size: 0.12rem;
    color: #737373;
    line-height: 0.3rem;
  }
  .ullist li:nth-child(3){
    font-size: 0.12rem;
    color: #000000;
    line-height: 0.5rem;
  }
  .inputbox {
    display: flex;
    position: absolute;
    right: 0.3rem;
    top: -0.1rem;
  }

  .inputbox input {
    width: 0.3rem;
    height: 0.25rem;
    margin-top: 0.7rem;
    background:none;
    outline:0;
    border: 1px solid #e1e1e1;

  }

  .inputbox li:nth-child(2) input {
    height: 0.23rem;
  }
</style>



