<template>
  <section class="time-limit">
    <ul class="time-limit-ul">
      <li>限时购</li>
      <li>02</li>
      <li class="colon">:</li>
      <li>00</li>
      <li class="colon">:</li>
      <li>35</li>
      <li>更多<i class="iconfont icon-arrow-left"></i></li>
    </ul>
    <ul class="time-limit-uzi">
      <li :key="i" v-for="(n,i) in ti">
        <img :src="n.imgas">
        <p>{{n.numberr}}<s>{{n.del}}</s></p>
      </li>
    </ul>
  </section>
</template>

<script>

    export default {
        name: "IndexTimeLimit",
      props:["ti"],
    }
</script>

<style scoped>
  .time-limit {
    width: 100%;
    height: 3.72rem;
    background: #ffffff;
    margin-bottom: 0.1rem;
  }
  .time-limit-ul {
    display: flex;
    margin-left: 0.17rem;
    height: 0.54rem;
    align-items: center;
  }

  .time-limit-ul li:nth-child(2n+1) {
    font-size: 0.17rem;
  }

  .time-limit-ul li:nth-child(2n) {
    font-size: 0.14rem;
    width: .19rem;
    height: 0.19rem;
    background: black;
    text-align: center;
    line-height: 0.19rem;
    border-radius: 5px;
    color: #ffffff;
  }

  .time-limit-ul li:nth-child(7) {
    margin-left: 1.5rem;
  }

  .time-limit-ul li .icon-arrow-left {
    font-size: 0.15rem;
  }

  .time-limit-ul li:nth-child(2) {
    margin-left: 0.1rem;
  }

  .colon {
    margin: 0 0.05rem;
  }
  .time-limit-uzi {
    width: 100%;
    height: 3.19rem;
    float: left;
  }

  .time-limit-uzi li {
    width: 1.16rem;
    float: left;
    height: 1.59rem;
    margin-left: 0.07rem;
  }

  .time-limit-uzi img {
    width: 1.15rem;
    height: 1.15rem;
    background: #f5f5f5;
    display: block;
  }

  .time-limit-uzi p {
    color: #bf464b;
    font-size: 0.13rem;
    text-align: center;
    line-height: 0.3rem;
  }

  .time-limit-uzi p s {
    color: #7f7f7f;
    font-size: 0.1rem;
    margin-left: 0.03rem;
  }

</style>



