<template>
  <ul class="product">
    <li>
      <img :src="item.img">
      <p>{{item.pone}}</p>
      <p>{{item.ptwo}}</p>
      <p>{{item.pthere}}</p>
    </li>
  </ul>
</template>

<script>
  export default {
    name: "ProductList",
    props:["item","idx"]
  }
</script>

<style scoped>
  .product {
    width: 3.55rem;
    margin: 0 auto;

  }

  .product li {
    height: 2.92rem;
    width: 1.73rem;
    float: left;
    margin-left: 0.04rem;
    margin-bottom: 0.1rem;
  }

  .product li:nth-child(2n) {
    margin-left: 0.05rem;
  }

  .product li img {
    height: 1.73rem;
    width: 100%;
    background: #f4f4f4;
    display: block;
  }

  .product li p:nth-child(2) {
    width: 100%;
    height: 0.35rem;
    background: #f1ece2;
    color: #9f8a5f;
    font-size: 0.16rem;
    text-align: center;
    line-height: 0.35rem;
  }
  .product li p:nth-child(3) {
    color: #000000;
    font-size: 0.15rem;
    line-height: 0.35rem;
    margin-left: 0.1rem;
  }
  .product li p:nth-child(4) {
    font-size: 0.16rem;
    color:darkred;
    margin-left: 0.1rem;
  }
</style>



