<template>
    <section class="swiperin">
      <div class="swiper-container s3">
        <div class="swiper-wrapper">
          <div class="swiper-slide":key="i" v-for='(n,i) in sw'><img :src="n.imgs" ></div>

        </div>
        <!-- Add Pagination -->
        <div class="swiper-pagination"></div>
      </div>
    </section>
</template>

<script>

    export default {
        name: "PartSwiper",
      props:["sw"],
      updated() {

        new Swiper('.s3', {
          pagination: {
            el: '.swiper-pagination',
            type: 'fraction',
          },
          loop:true,
        });
      }

    }
    import Swiper from "swiper"
    import "../../../node_modules/swiper/dist/css/swiper.css"
</script>

<style scoped>
  .swiperin{
    width: 100%;
    height:2.51rem;

  }
  .s3 .swiper-pagination {
    font-size: 0.1rem;
    width: 0.5rem;
    border-radius: 5px;
    height:0.15rem;
    background: white;
    top: 93%;
    left: 90%;
  }
  .s3 img {
    width: 100%;
    height: 100%;
  }
</style>



