<template>
  <section class="list">
    <div class="home-header">
      <p>男士外套</p>
      <p>自在潇洒穿著感</p>
    </div>
    <cost-product :item="n" :idx="i" :key="i" v-for="(n,i) in co"></cost-product>
    <div class="backtop">
      <i class="iconfont icon-arrowDown"></i>
    </div>
  </section>
</template>

<script>
  import CostProduct from "./CostProduct";

  export default {
    name: "CostumeCoco",
    props: ["co"],
    components: {CostProduct}
  }
</script>

<style scoped>
  .list {
    width: 100%;
    height: 100%;
    background: white;
  }

  .home-header {
    width: 100%;
    height: 0.6rem;
  }

  .home-header p {
    text-align: center;
  }

  .home-header p:last-child {
    font-size: 0.14rem;
    color: #acacac;

  }

  .home-header p:first-child {
    font-size: 0.16rem;
    padding-top: 0.08rem;
    line-height: 0.23rem;
  }

  .backtop {
    width: 0.5rem;
    height: 0.5rem;
    background: white;
    border-radius: 50%;
    border: 1px solid #f6f6f6;
    position: fixed;
    right: 1%;
    display: flex;
    bottom: 10%;
    align-items: center;
    justify-content: center;
  }

  .icon-arrowDown {
    font-size: 0.2rem;
    display: block;

  }

</style>



