<template>
  <div  class="AAA">
    <particulars-header></particulars-header>
    <div class="shen">
      <part-swiper :sw="parts.swipers"></part-swiper>
      <part-introduce></part-introduce>
      <part-options></part-options>
      <part-bottom :pa="parts.part"></part-bottom>
    </div>
    <part-footer></part-footer>
  </div>
</template>

<script>
  import ParticularsHeader from "../components/particulars/ParticularsHeader";
  import PartSwiper from "../components/particulars/PartSwiper";
  import PartIntroduce from "../components/particulars/PartIntroduce";
  import PartOptions from "../components/particulars/PartOptions";
  import PartBottom from "../components/particulars/PartBottom";
  import PartFooter from "./PartFooter";

  const AJAXURL = 'http://127.0.0.1:5000/aaa';

  export default {
    name: "WangYiParticulars",
    data() {
      return {
        parts: []
      }
    },
    created() {
      this._initPageData()
    },
    components: {
      PartFooter,
      PartBottom,
      PartOptions,
      PartIntroduce,
      PartSwiper,
      ParticularsHeader
    },
    methods: {

      _initPageData() {
        fetch(AJAXURL).then(res => {
          res.json().then(data => {
            this.parts = data;
            console.log(data)
          })
        })
      }

    }
  }
</script>

<style scoped>
  .AAA {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .shen {
    width: 100%;
    overflow: hidden;
    flex: 1;
    overflow-y: scroll;
    height: 100%;
    background: #ffffff;
  }
</style>



