<template>
  <section class="sliderimg">
    <div class="swiper-container s1">
      <div class="swiper-wrapper">
        <div class="swiper-slide" :key="i" v-for='(s,i) in swipers'><img :src="s.imgs"></div>
      </div>
      <div class="swiper-pagination"></div>
    </div>
  </section>
</template>

<script>
  export default {
    name: "IndexSwiper",
    props:["swipers"],

    updated() {
       new Swiper(' .s1', {
        autoplay: {
          stopOnLastSlide: true,
        },
        loop: true,
        pagination: {
          el: '.swiper-pagination',
        },
      })
    }
  }
  import Swiper   from "swiper"
  import "../../../node_modules/swiper/dist/css/swiper.css";


</script>

<style scoped>
  .sliderimg {
    width: 100%;
    height: 1.97rem;
  }

  .sliderimg .s1 .swiper-pagination {
    top: 38%;
    left: 0;
  }
  .sliderimg .s1 img {
    width: 100%;
    height: 100%;
  }

</style>
<style>
  .sliderimg .s1 .swiper-pagination-bullet{
    background: grey;
    width:0.25rem;
    height:0.025rem;
  opacity: 1;
  }
  .sliderimg .s1 .swiper-pagination-bullet-active {
    width:0.25rem;
    border-radius:0.1rem ;
    background:#ffffff;
    top:2rem;
  }
</style>



