<template>
  <div class="AAA">
    <index-header></index-header>
    <div class="shen">
      <index-swiper :swipers="indexone.swiper"></index-swiper>
      <index-snatch :sn="indexone.snatch"></index-snatch>
      <index-private-custom :snw="indexone.swipertwo"></index-private-custom>
      <index-time-limit :ti="indexone.times"></index-time-limit>
      <index-new-product :ne="indexone.newprod"></index-new-product>
      <index-recommend :re="indexone.recomm"></index-recommend>
      <indx-sell-well ></indx-sell-well>
      <index-activity></index-activity>
      <index-brand :br="indexone.brand"></index-brand>
      <index-daily :da="indexone.dal"></index-daily>
    </div>
    <index-footer></index-footer>
  </div>
</template>

<script>
  import IndexHeader from "../components/index/IndexHeader";
  import IndexFooter from "../components/index/IndexFooter";
  import IndexSwiper from "../components/index/IndexSwiper";
  import IndexSnatch from "../components/index/IndexSnatch";
  import IndexPrivateCustom from "../components/index/IndexPrivateCustom";
  import IndexTimeLimit from "../components/index/IndexTimeLimit";
  import IndexNewProduct from "../components/index/IndexNewProduct";
  import IndexRecommend from "../components/index/IndexRecommend";
  import IndxSellWell from "../components/index/IndexSellWell";
  import IndexActivity from "../components/index/IndexActivity";
  import IndexBrand from "../components/index/IndexBrand";
  import IndexDaily from "../components/index/IndexDaily";

  const AJAXURL = 'http://127.0.0.1:5000/aaa';


  export default {
    name: "WangYiIndex",

    data() {
      return {
        indexone:[]
      }
    },
    created() {
      this._initPageData()
    },
    components: {
      IndexDaily,
      IndexBrand,
      IndexActivity,
      IndxSellWell,
      IndexRecommend,
      IndexNewProduct,
      IndexTimeLimit, IndexPrivateCustom, IndexSnatch, IndexSwiper, IndexFooter, IndexHeader
    },
    methods: {

      _initPageData() {
        fetch(AJAXURL).then(res => {
          res.json().then(data => {
            this.indexone = data;
            console.log(data)
          })
        })
      }

    }

  }

</script>

<style scoped>
  .AAA {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .shen {
    width: 100%;
    overflow: hidden;
    flex: 1;
    overflow-y: scroll;
  }
</style>



