<template>
  <ul class="daily-ul" >
    <li   :key="i" v-for="(a,i) in navss">
      <span>{{a.imgnavs}}<img :src="a.imgnav"></span>
      <b>{{a.bnav}}</b>
      <p>{{a.pnav}}<br/>{{a.pnar}}</p>
      <i>{{a.inav}}</i>
    </li>

  </ul>
</template>

<script>
    export default {
        name: "DailyUl",
      props: ["navss", "idx"]
    }
</script>

<style scoped>
  .daily-ul {
    width: 100%;
    display: flex;
    white-space: nowrap;
    overflow-x: auto;
    overflow-y: hidden;
  }

  .daily-ul li {
    width: 1.03rem;
    height: 2.04rem;
    margin-left: 0.1rem;
    margin-top: 0.1rem;
  }

  .daily-ul li span {
    height: 1.07rem;
    width: 1.03rem;
    display: block;
    background: #f4f4f4;
    font-size: 0.14rem;
    text-align: center;
    line-height: 1.03rem;
  }

  .daily-ul li span img {
    display: block;
    width: 1rem;
    height: 1.07rem;
    border: none;
  }

  .daily-ul b {
    width: 0.77rem;
    height: 0.2rem;
    background: #f48f18;
    font-size: 0.14rem;
    color: white;
    border-radius: 5px;
    margin-top: 0.05rem;
    margin-left: 0.05rem;
    text-align: center;
    display: block;
  }

  .daily-ul p {
    font-size: 0.14rem;
    margin-left: 0.05rem;
  }

  .daily-ul i {
    font-size: 0.14rem;
    display: block;
    color: #b4282d;
    margin-left: 0.05rem;
  }
</style>



